import React, { Fragment, useEffect } from 'react';
import { withRouter } from 'next/router';
import { createClient }  from 'contentful';
import ReactMarkdown from 'react-markdown';
import Error from 'next/error';
import Layout from '../components/layout/main';
import { CONTENTFUL_SPACE_ID, CONTENTFUL_ACCESS_TOKEN } from '../defaults/ContentfulKeys';

const Post = withRouter(({ errorCode, post, router }) => {

  if(errorCode) {
    return <Error statusCode={errorCode} />;
  }

  const imageURL = `
    http//images.ctfassets.net/
    /${post.fields.heroImage.sys.space.sys.id}
    /${post.fields.heroImage.sys.id}
    /${CONTENTFUL_ACCESS_TOKEN}
    /${post.fields.heroImage.fields.file.fileName}
    ?fit=fill&w=${1000}&h=${500}`;

  return (
    <Fragment>
      <Layout>
        <div className="post">
          <h1>{post.fields.title}</h1>
          <img src={imageURL}/>
          <div className="post__content">
            <ReactMarkdown source={post.fields.body} />
          </div>
          <div className="post__source">Fonte: {JSON.stringify(post.fields.source)}</div>
          <div className="post__author">Sobre o autor: {JSON.stringify(post.fields.author)}</div>
          <div className="post__tags">{post.fields.tags.join(', ')}</div>
        </div>
      </Layout>
      <style jsx>{`

        h1,
        h2,
        h3 {
          font-family: stolzl, sans-serif;
          font-weight: 700;
          font-style: normal;
        }

        h1 {
          margin: 20px 0 10px 0;
          font-size: 50px;
          line-height: 50px;
          height: 60px;
          background-image: linear-gradient(to bottom, #4CB8C4, #3CD3AD);
          color: transparent;
          -webkit-background-clip: text;
        }

        .post {
          padding: 0 20px;
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: center;
        }

        .post__content {
          font-family: stolzl,sans-serif;
          font-weight: 100;
          font-size: 16px;
          line-height: 22px;
          width: 700px;
          margin: auto;
          text-align: justify;
        }

      `}</style>
    </Fragment>
  );
});

Post.getInitialProps = async ({ query }) => {
  const { slug } = query;

  const client = createClient({
    space: CONTENTFUL_SPACE_ID,
    accessToken: CONTENTFUL_ACCESS_TOKEN
  });

  const entries = await client.getEntries({
    content_type: 'blogPost',
    'fields.slug[in]': slug,
  });

  return {
    post: entries.items[0]
  };
}

export default Post;
